import React from "react";
import {
  Formik,
  Form,
  Field,
  ErrorMessage,
  setNestedObjectValues
} from "formik";
import * as Yup from "yup";
import "../src/App.css";
import Axios from "axios";
import { timeout } from "q";
import {
  ToastsContainer,
  ToastsStore,
  ToastsContainerPosition
} from "react-toasts";
const httpClient = Axios.create({
  timeout: 5000,
  headers: { "content-type": "application/json" }
});
const SignupSchema = Yup.object().shape({
  password: Yup.string()
    .required("رمز عبور خود را وارد کنید")
    .min(6, "حداقل طول رمز عبور ۶ می باشد"),
  passwordConfirm: Yup.string().test(
    "passwords-match",
    "با رمز عبور مطابقت ندارد",
    function(value) {
      return this.parent.password === value;
    }
  )
});

const { innerHeight, innerWidth } = window;

class App extends React.Component {
  constructor(props) {
    super(props);
    this.state = { password: "", buttonDisabled: false };
  }

  handleSubmit(url, userToken, password) {
    if(this.state.buttonDisabled) {      
     ToastsStore.warning("لینک ثبت رمز عبور جدید منقضی شده است")
    }    
    else  {
      httpClient({
        method: "POST",
        url: url + "/users/reset",
        params: {
          token: userToken,
          validPassword: password
        }
      })
        .then(response => {
          ToastsStore.success("ثبت رمز جدید با موفقیت انجام شد");
        })
        .catch(error => {
          ToastsStore.error("عدم ارتباط با سرور");
        });
      this.setState({buttonDisabled: true})
    }

  }

  buttonBackGroundHandler(numErrors) {
    if (
      numErrors == 0 &&
      this.state.password != "" &&
      !this.state.buttonDisabled
    )
      return "blue";
    return "gray";
  }

  render() {
    return (
      <div className="App">
        <Formik
          initialValues={{
            password: "",
            passwordConfirm: ""
          }}
          validationSchema={SignupSchema}
          onSubmit={values => {
            this.handleSubmit(
              window.location.href.split("/")[0],
              window.location.href
                .split("?")[1]
                .split("=")[1]
                .split("&")[0],
              values.password
            );
          }}
        >
          {props => (
            <Form>
              <div className="labelStyle">رمز عبور جدید</div>
              <div>
                <Field
                  type="password"
                  validate={value => {
                    this.setState({ password: value });
                  }}
                  name="password"
                  className="boxStyle"
                />
              </div>
              <div className="errorStyle">{props.errors.password}</div>

              <div className="labelStyle" style={{ marginTop: 30 }}>
                تکرار رمز عبور جدید
              </div>
              <div>
                <Field
                  type="password"
                  name="passwordConfirm"
                  className="boxStyle"
                />
              </div>
              <div className="errorStyle">{props.errors.passwordConfirm}</div>
              <button
                className="buttonStyle"
                style={{
                  backgroundColor: this.buttonBackGroundHandler(
                    Object.keys(props.errors).length
                  ),
                  borderColor: this.buttonBackGroundHandler(
                    Object.keys(props.errors).length
                  )
                }}
              >
                ثبت رمز عبور جدید
              </button>
              <div>
                {}
              </div>
              <ToastsContainer
                position={ToastsContainerPosition.BOTTOM_RIGHT}
                store={ToastsStore}
              />
            </Form>
          )}
        </Formik>
      </div>
    );
  }
}

export default App;
